import React, { Component } from 'react';
import { Modal, Form, Input, Select } from 'antd';
// import { getAction, postAction } from '@/axios';

const FormItem = Form.Item;
const Option = Select.Option;

//
const formItemLayout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 17, style: { paddingLeft: '10px' } },
};

class AddOrEditModal extends Component {
    state = {
        roleLists: [],
    }

    componentDidMount(){
        this.getRoleLists();
    }

    getRoleLists(){
        let res = {"success":true,"msg":"","obj":[{"id":1,"roleName":"","remark":"","createTime":1536549032000,"updateTime":1536550539000},{"id":31,"roleName":"","remark":"","createTime":1541034938000,"updateTime":1541034938000},{"id":33,"roleName":"","remark":"","createTime":1542697696000,"updateTime":1542698189000},{"id":34,"roleName":"","remark":"","createTime":1543396029000,"updateTime":1544107599000},{"id":35,"roleName":"","remark":"","createTime":1544107460000,"updateTime":1544107517000},{"id":36,"roleName":"","remark":"","createTime":1544107539000,"updateTime":1544107539000},{"id":37,"roleName":"","remark":"","createTime":1544107561000,"updateTime":1544107561000},{"id":38,"roleName":"","remark":"","createTime":1574128717000,"updateTime":1574128717000},{"id":39,"roleName":"","remark":"","createTime":1574128739000,"updateTime":1574128739000}],"errorCode":null};
        this.setState({
            roleLists: (res.obj && res.obj[0] && res.obj.map(item => {
                return {
                    name: item.roleName,
                    value: item.id
                }
            })) || []
        })
        // getAction("/bigdata/system/role/list?sourceCode=pc").then((res)=>{
        //     if (res.success) {
        //         this.setState({
        //             roleLists: (res.obj && res.obj[0] && res.obj.map(item => {
        //                 return {
        //                     name: item.roleName,
        //                     value: item.id
        //                 }
        //             })) || []
        //         })
        //     }
        // })
    }

    onOk = () => {
        //
        this.props.form.validateFields(
            (err, values) => {
                if (!err) {
                    this.props.onOk();
                    // let { userName, name, sex, accountCode, roleId } = values;
                    // let reqUrl = "/bigdata/system/user/save";
                    // let data = {
                    //     sourceCode: "pc", // (PC)
                    //     userName, //  string
                    //     name, //  string
                    //     sex: sex - 0, //  num init  statestring 
                    //     accountCode, // / string
                    //     roleId:roleId - 0, //  num init  statestring 
                    // }
                    // if (this.props.editData) {
                    //     reqUrl = "/bigdata/system/user/update";
                    //     data.id = this.props.editData.id;
                    // }
                    // postAction(reqUrl, data, 0, 1).then((res) => {
                    //     if (res.success) {
                    //         message.success("");
                    //         this.props.onOk();
                    //     } else {
                    //         message.error(res.obj);
                    //     }
                    // })
                }
            },
        );
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        let { editData } = this.props;
        let { roleLists } = this.state;
        let roleId;
        if (editData) {
            roleLists.forEach((item) => {
                if (item.name === editData.roles) {
                    roleId = item.value
                }
            });
        }

        return (
            <Modal
                title={editData?'':''}
                visible={true}
                onOk={this.onOk}
                onCancel={this.props.onCancel}
            >
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator('userName', {
                        rules: [{
                            required: true,
                            message: ''
                        }],
                        initialValue: editData && editData.username
                    })(
                        <Input placeholder="" />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator('name', {
                        rules: [{
                            required: true,
                            message: ''
                        }],
                        initialValue: editData && editData.name
                    })(
                        <Input placeholder="" />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator('sex', {
                        rules: [{
                            required: true,
                            message: ''
                        }],
                        initialValue: editData && editData.sex
                    })(
                        <Select placeholder="" style={{ width: "100%" }}>
                            <Option value={1}></Option>
                            <Option value={2}></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="/">
                    {getFieldDecorator('accountCode', {
                        rules: [{
                            required: true,
                            message: '/'
                        }],
                        initialValue: editData && editData.accountCode
                    })(
                        <Input placeholder="/" />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator('roleId', {
                        rules: [{
                            required: true,
                            message: ''
                        }],
                        initialValue: roleId
                    })(
                        <Select placeholder="" style={{ width: "100%", marginBottom: 8 }} onChange={this.roleIdChange}>
                            {
                                roleLists && roleLists[0] && roleLists.map((item, index) => {
                                    return (
                                        <Option value={item.value} key={index}>{item.name}</Option>
                                    )
                                })
                            }
                        </Select>
                    )}
                </FormItem>
            </Modal>
        )
    }
}

export default Form.create()(AddOrEditModal);