import React from 'react';
import { Input, Select, Button } from 'antd';
// import { getAction} from '@/axios';
const Option = Select.Option;

export default class Filter extends React.Component {
    state = {
        username: "", // 
        name: "", // 
        code: "", // /
        role:"", // 
        roleLists: [], // 
    }
	
	
	componentDidMount(){
        this.getRoleLists();
        this.handleSearch();
    }
    
    getRoleLists(){
        let res = {"success":true,"msg":"","obj":[{"id":1,"roleName":"","remark":"","createTime":1536549032000,"updateTime":1536550539000},{"id":31,"roleName":"","remark":"","createTime":1541034938000,"updateTime":1541034938000},{"id":33,"roleName":"","remark":"","createTime":1542697696000,"updateTime":1542698189000},{"id":34,"roleName":"","remark":"","createTime":1543396029000,"updateTime":1544107599000},{"id":35,"roleName":"","remark":"","createTime":1544107460000,"updateTime":1544107517000},{"id":36,"roleName":"","remark":"","createTime":1544107539000,"updateTime":1544107539000},{"id":37,"roleName":"","remark":"","createTime":1544107561000,"updateTime":1544107561000},{"id":38,"roleName":"","remark":"","createTime":1574128717000,"updateTime":1574128717000},{"id":39,"roleName":"","remark":"","createTime":1574128739000,"updateTime":1574128739000}],"errorCode":null};
        this.setState({
            roleLists: (res.obj && res.obj[0] && res.obj.map(item => {
                return {
                    name: item.roleName,
                    value: item.id
                }
            })) || []
        })
        // getAction("/bigdata/system/role/list?sourceCode=pc").then((res)=>{
        //     if (res.success) {
        //         this.setState({
        //             roleLists: (res.obj && res.obj[0] && res.obj.map(item => {
        //                 return {
        //                     name: item.roleName,
        //                     value: item.id
        //                 }
        //             })) || []
        //         })
        //     }
        // })
    }

    handleChangeInput(type, e){
        if(type === 0){
            this.setState({username: e.target.value})
        }else if(type === 1){
            this.setState({name: e.target.value})
        }else if(type === 2){
            this.setState({code: e.target.value})
        }
    }

    handleChangeSelect = (value) => {
        this.setState({role: value})
    }

    handleSearch = () => {
        let filterValue = {
            sourceCode: 'pc',
    		username : this.state.username,
    		name: this.state.name,
    		accountCode: this.state.code,
            roleId: this.state.role,
    	}
    	
    	this.props.getFilterValue(filterValue);
    }
	
	render () {
        let { username, name, code, role, roleLists} = this.state;
		return (
			<div style={{display: 'flex', paddingBottom: 20, lineHeight: '40px'}}>
				<div style={{marginRight: 10}}>
                    <span></span>
                    <Input 
                        value={username} 
                        onChange={this.handleChangeInput.bind(this, 0)} 
                        placeholder=""
                        style={{width: 130}} 
                    />
                </div>
				
				<div style={{marginRight: 10}}>
                    <span></span>
                    <Input 
                        value={name} 
                        onChange={this.handleChangeInput.bind(this, 1)} 
                        placeholder=""
                        style={{width: 130}} 
                    />
                </div>
				
				<div style={{marginRight: 10}}>
                    <span>/</span>
                    <Input 
                        value={code} 
                        onChange={this.handleChangeInput.bind(this, 2)} 
                        placeholder="/"
                        style={{width: 150}} 
                    />
                </div>
				
				<div style={{marginRight: 10}}>
                    <span></span>
                    <Select placeholder='' style={{ width: 150 }} onChange={this.handleChangeSelect} value={role}>
                        <option value=""></option>
                        {
                            roleLists && roleLists[0] && roleLists.map((item, index)=>(
                                <Option value={item.value} key={index}>{item.name}</Option>
                            ))
                        }
                    </Select>
                </div>
				
				<Button type="primary" onClick={this.handleSearch} style={{margin: '4px 10px 0 0'}}></Button>
				<Button type="primary" icon="plus" style={{marginTop: 4}} onClick={this.props.addUser}></Button>
			</div>
		)
	}
}
